/* color palette: https://www.schemecolor.com/cartwheels.php */

body {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #9a899a;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.board {
  border: 4px solid #d48695;
  border-radius: 5px;
  width: 400px;
  height: 400px;
  margin: 0 auto;
  display: grid;
  grid-template: repeat(3, 1fr) / repeat(3, 1fr);
}

.square {
  background: #faddd2;
  border: 1px dotted #d48695;
  font-size: 50px;
  font-weight: 500;
  color: #6a5971;
  cursor: pointer;
  box-sizing: border-box;
  outline: none;
}

.playerInfo {
  font-size: 20px;
  text-align: center;
  color: aliceblue;
}

.startGameBtn {
  display: block;
  align-content: center;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  margin: 20px auto;
  font-size: 15px;
  border: 2px dotted aliceblue;
  border-radius: 4px;
  padding: 5px;
  background-color: #ceb1ab;
  cursor: pointer;
  outline-color: #d48695;
}

.startGameBtn:hover {
  transform: scale(1.05);
}

.O {
  color: #9a899a;
}
